import React from 'react';
import { BaseCalendar } from 'shared/BaseCalendar';
import { MobileCalendar } from 'shared/MobileCalendar';
import { CalendarProps } from 'shared/types/calendar';

import styles from 'shared/BaseCalendar/externalColorStyles.scss';

export const Calendar: React.FC<Omit<CalendarProps, 'colorStyles'> & { isMobile?: boolean }> = ({ isMobile, ...props }) => (
  isMobile
    ? <MobileCalendar { ...props } />
    : <BaseCalendar { ...props } colorStyles={ styles } />
);
